/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/

@font-face {
  font-family: 'NovecentoSans';
  src: url(/ra-project05/wp-content/themes/ra-project05/assets/fonts/Novecento/Novecentosanswide-Normal.ttf);
  font-weight: normal;
  font-style: normal; 
}

@font-face {
  font-family: 'NovecentoSans';
  src: url(/ra-project05/wp-content/themes/ra-project05/assets/fonts/Novecento/Novecentosanswide-DemiBold.ttf);
  font-weight: 700;
  font-style: normal; 
}

body,
button,
input,
select,
textarea {
	color: $color__text-main;
	font-family: $font__main;
	@include font-size(1);
	line-height: $font__line-height-body;
}

label {
	text-transform: uppercase;
	font-family: 'NovecentoSans';
}
/* Headings */

h1, h2, h3, h4, h5, h6 {
	clear: both;
	font-family: 'NovecentoSans';
	font-weight: normal;
	text-transform: uppercase;
}

/* Copy */

p {
	margin-bottom: $base__space-unit;
	font-family: 'Merriweather', serif;
}

footer li, footer a {
	font-family: 'Merriweather', serif;
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	margin: 0 $base__space-unit;
}

address {
	margin: 0 0 $base__space-unit;
}

pre {
	background: $color__background-pre;
	font-family: $font__pre;
	@include font-size(0.9375);
	line-height: $font__line-height-pre;
	margin-bottom: 1.6rem;
	max-width: 100%;
	overflow: auto;
	padding: 1.6rem;
}

code, kbd, tt, var {
	font-family: $font__code;
	@include font-size(0.9375);
}

abbr, acronym {
	border-bottom: 1px dotted $color__border-abbr;
	cursor: help;
}

mark, ins {
	background: $color__background-ins;
	text-decoration: none;
}

big {
	font-size: 125%;
}

h2 {
	padding: 20px 0;
}
